import React, { useEffect, useRef, useState } from 'react'
import Config from './config';
import './App.css'
import * as firebase from 'firebase'


function getRandomArbitrary(min, max) {
  return Math.random() * (max - min) + min;
}

function App() {  

  // recognition

  const SpeechRegognition =
  window.SpeechRecognition || window.webkitSpeechRecognition;

  const recognition = new SpeechRegognition();

  recognition.continuous = false;

  recognition.onstart = function () {
  console.log("en ecoute");
  };

  recognition.onend=function(){recognition.start()}

  recognition.onresult = function (e){
    let voice=e.results[0][0]["transcript"]

    let count=0
    for (let i = 0; i < voice.length; i++) {
      if(voice.includes("capter".charAt(i))){
      count=count+1
      }
    }
    let count2=0
    for (let i = 0; i < voice.length; i++) {
      if(voice.includes("stop".charAt(i))){
      count2=count2+1
      }
    }
    if(Math.abs("capter".length-count<2)){
      setState(true)
    }
    if(Math.abs("stop".length-count2)<2){
      setState(false)
    }
    console.log(voice)
    if(voice.includes("arrêt") || voice.includes("démarre") ){
      arretFByvoice(voice)
    } else if(voice.includes("capte")){
        setState(true)
    } else if(voice.includes("stop")){
      setState(false)
    } else if(voice.includes("envo")){
      souhaitableValueF()
    }
    else {
      recognition.stop()
    }
  }
  useEffect(()=>{
    recognition.start()
  },[])
  // fin recognition

    function changeState(e){
       if (state===true){
         setState(false)
         e.target.style.background="#d34836"
       } else {
         setState(true)
         e.target.style.background="green"
       }
    }
    function arretFByvoice(voice){
      if(voice.includes("arrêt")){
        setArret("démarer")
    } else if (voice.includes("démarre")){
      setArret("arrêter")
    }
    }

    function arretF(e){
      if(arret==="arrêter"){
        setArret("démarer")
        e.target.style.background="#d34836"
      } 
      else {
        setArret("arrêter")
        e.target.style.background="green"
      }
    }

    function souhaitableValueF(){
      firebase.firestore().collection("Souhaitable")
      .add({date:new Date(), temperature:souhaitableValue.current.value, text:souhaitableText.current.value})
      .then(d=>console.log(d))
      .catch(r=>console.log(r))      
    }
//
    const reference=useRef(null)

    const  [arret, setArret]=useState("arrêter")
    const  [state, setState]=useState(false)

    //  co

//
    const souhaitableValue=useRef(null)
    const souhaitableText=useRef(null)

    useEffect(()=>{
      const timer=setInterval(function(){
      if(arret==="arrêter"){
        if(state===false){
          firebase.firestore().collection("Chambre1")
          .add({date:new Date(), temperature:parseInt(getRandomArbitrary(1,3))})
          .then(d=>console.log(d))
          .catch(r=>console.log(r))
        }
        else {
          firebase.firestore().collection("Chambre1")
          .add({date:new Date(), temperature:parseInt(reference.current.value)}) 
          .then(d=>console.log(d))
        }
      } else {      
        firebase.firestore().collection("Chambre1")
        .add({date:new Date(), temperature:"Aucune température capter"})
        .then(d=>console.log(d))
        .catch(r=>console.log(r))
      }
    },5000) 
    return function(){clearInterval(timer)}

    },[state, arret])
 
  return (
    <div className="App-header">
    <div className="global">
    <div className="1">
    <input type="number" ref={reference}></input><br/>
    <button onClick={changeState}>{"Forcer valeur :"+JSON.stringify(state)}</button>
    </div>
    <div className="2">
      <input type="number" ref={souhaitableValue}></input><br/>
      <textarea type="text" ref={souhaitableText}></textarea><br/>
      <button style={{background:"#000000"}} onClick={souhaitableValueF}>Envoyer consigne</button>
    </div>
    <br/>
    <div className="3">
      <button className="bouton" onClick={arretF}>{arret+" capteur"}</button>
    </div>
    </div>
    </div>
  );
}

export default App;



